:root{
    --square-side-length: 40px;
    --crossword-size:7
}

.crossword {
    
}
.actions-menu{
    display:grid;
    grid-template-columns:repeat(3,1fr);
    gap: 10px;
}
.board-and-clues {
    gap:10px;
    display:grid;
    grid-template-columns: repeat(3,1fr);
}
.square {
    width: var(--square-side-length);
    height: var(--square-side-length);
    border: 1px solid #282828;
    color: black;
    text-align: center;
    font-size: x-large;
}
.square-black {
    background-color: black;
}
.square-wrong {
    color:red;
}
.square-correct{
    color:green
}
.square-user {
    background-color:#ffcc00;
    opacity: .9;
}
.square-in-row {
    background-color: #8BCCF7;

}
.board {
  display: grid;
  grid-template-columns: repeat(var(--crossword-size), var(--square-side-length));
  grid-template-rows: repeat(var(--crossword-size), var(--square-side-length));
  gap: 0px 0px;
}
.clue-highlighted {
    background-color: #8BCCF7
}
.clue-number {
    font-size: large;
    font-weight: bold;
}
.grid-clue-number {
    font-size: small;
    color: black;
    /* margin-right: 75%; */
    /* margin-top: 10% */
}